getUser1() {
axios
.get('ppartymember/queryByUserId', {
params: {
userid: this.$route.params.userId
}
})
.then(res => {
this.userId = res.data.userid;
this.departmentid = res.data.departmentid;
this.name = res.data.name;
this.departmentname = res.data.departmentname;
})
.catch(err => {
console.log(err);
});
},
show(index) {
this.$refs.previewer.show(index);
},

spread() {
this.spr = true;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
folding() {
this.spr = !this.spr;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
noSpread() {
this.noSpr = true;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
noFolding() {
this.noSpr = !this.noSpr;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
logIndexChange(arg) {
console.log(arg);
}
},
mounted() {
this.list1Selected = this.list1.find(item => item.moduleId == this.$route.params.moduleid);
this.getUser1();
this.getList1();
},
data() {
return {
scoreTime: '',
num: 0,
userId: '',
adderName: '',
name: '',
departmentid: '',
content1: '',
content2: '',
departmentname: '',
activeData: {},
picInfo: [],
list: [],
list1: [{ text: '口头汇报', moduleId: 11 }, { text: '书面汇报', moduleId: 12 }],
list1Selected: {},
content: {},
imgpics: [],
imgs: '',
spr: false,
noSpr: false,
nobtnPack: false,
nobtnAn: false,
btnPack: false,
btnAn: false,
peopleNum: null,
participants: null,
Noparticipants: null,
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index];

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
}
};
